import React from 'react';
import './main-menu.css';
interface MainMenuProps {
  highScore: number;
  onStartGame: () => void;
}

const MainMenu: React.FC<MainMenuProps> = ({ highScore, onStartGame }) => {
  return (
    <div className="main-menu">
      <h1 className="title">Vlkommen till Simones Ordspel</h1>
      
      {/* Menu Buttons */}
      <div className="menu-buttons">
        <button className="menu-button" onClick={onStartGame}>
          Starta Spel
        </button>
        <button className="menu-button" disabled>
          Instllningar (Kommer snart)
        </button>
        <button className="menu-button" disabled>
          Om Spelet
        </button>
      </div>
      
      {/* High Score */}
      <div className="high-score">
        <h2>Hgsta pong: {highScore}</h2>
      </div>
    </div>
  );
};

export default MainMenu;
