import React, { useState, useCallback, useEffect, useRef } from 'react';
import LetterGrid from '../LetterGrid/LetterGrid';
import WordInput from '../WordInput/WordInput';
import WordList from '../WordList/WordList';
import Score from '../Score/Score';
import { Button, IconButton } from '@mui/material';
import ShuffleIcon from '@mui/icons-material/Shuffle';
import RestartAltIcon from '@mui/icons-material/RestartAlt'; // Reset icon
import './game.css';
interface WordSet {
  letters: string[];
  centralLetter: string;
}

const MIN_WORDS = 15;

interface GameProps {
  validWords: string[];
  onGameEnd: (score: number) => void;
  onCancelGame: () => void;
}

function Game({ validWords, onGameEnd, onCancelGame }: GameProps) {
  const [submittedWords, setSubmittedWords] = useState<string[]>([]);
  const [score, setScore] = useState<number>(0);
  const [letters, setLetters] = useState<string[]>([]);
  const [centralLetter, setCentralLetter] = useState<string>('');
  const [errorMessage, setErrorMessage] = useState<string>(''); 
  const [currentWord, setCurrentWord] = useState<string>(''); 
  const [filteredValidWords, setFilteredValidWords] = useState<string[]>([]);

  const foundWordsRef = useRef<HTMLDivElement | null>(null);
  const soundRef = useRef<HTMLAudioElement | null>(null);
  const errorSoundRef = useRef<HTMLAudioElement | null>(null);

  useEffect(() => {
    soundRef.current = new Audio(process.env.PUBLIC_URL + '/assets/found-word.mp3');
    errorSoundRef.current = new Audio(process.env.PUBLIC_URL + '/assets/error-sound.mp3');
  }, []);

  useEffect(() => {
    if (validWords.length > 0) {
      generateValidLetterSet(validWords);
    }
  }, [validWords]);

  useEffect(() => {
    if (foundWordsRef.current) {
      foundWordsRef.current.scrollTop = foundWordsRef.current.scrollHeight;
    }
  }, [submittedWords]);

  const endGame = () => {
    onGameEnd(score);
  };

  const cancelGame = () => {
    onCancelGame();
  };

  const filterValidWords = useCallback((wordArray: string[], letters: string[], centralLetter: string): string[] => {
    return wordArray.filter(word => {
      const wordLetters = word.toUpperCase().split('');
      return wordLetters.includes(centralLetter) && wordLetters.every(letter => letters.includes(letter));
    });
  }, []);

  const generateValidLetterSet = useCallback((wordArray: string[]) => {
    let possibleLetters: WordSet | null = null;

    while (!possibleLetters) {
      const randomLetters = generateRandomLetters();
      const centralLetter = randomLetters[Math.floor(Math.random() * randomLetters.length)];

      const validWordsForSet = filterValidWords(wordArray, randomLetters, centralLetter);

      if (validWordsForSet.length >= MIN_WORDS) {
        setLetters([...randomLetters.filter(l => l !== centralLetter).slice(0, 3), centralLetter, ...randomLetters.filter(l => l !== centralLetter).slice(3)]);
        setCentralLetter(centralLetter);
        setFilteredValidWords(validWordsForSet);
        possibleLetters = { letters: randomLetters, centralLetter };
      }
    }
  }, [filterValidWords]);

  const generateRandomLetters = (): string[] => {
    const alphabet = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'.split('');
    const randomLetters = new Set<string>();

    while (randomLetters.size < 7) {
      const randomLetter = alphabet[Math.floor(Math.random() * alphabet.length)];
      randomLetters.add(randomLetter);
    }

    return Array.from(randomLetters);
  };

  const isValidWord = useCallback((word: string): boolean => {
    const wordLetters = word.toUpperCase().split('');

    if (!wordLetters.includes(centralLetter)) {
      setErrorMessage('Ditt ord saknar den centrala bokstaven!');
      if (errorSoundRef.current) {
        errorSoundRef.current.play().catch((error) => {
          console.error('Error sound playback failed:', error);
        });
      }
      return false;
    }

    if (word.length < 3) {
      setErrorMessage('Ditt ord r fr kort, det mste ha minst 3 bokstver.');
      if (errorSoundRef.current) {
        errorSoundRef.current.play().catch((error) => {
          console.error('Error sound playback failed:', error);
        });
      }
      return false;
    }

    if (!wordLetters.every(letter => letters.includes(letter.toUpperCase()))) {
      setErrorMessage('Ditt ord innehller bokstver som inte finns bland de tilltna.');
      if (errorSoundRef.current) {
        errorSoundRef.current.play().catch((error) => {
          console.error('Error sound playback failed:', error);
        });
      }
      return false;
    }

    if (!filteredValidWords.includes(word.toLowerCase())) {
      setErrorMessage('Ditt ord finns inte i den svenska ordlistan.');
      if (errorSoundRef.current) {
        errorSoundRef.current.play().catch((error) => {
          console.error('Error sound playback failed:', error);
        });
      }
      return false;
    }

    setErrorMessage('');
    return true;
  }, [centralLetter, letters, filteredValidWords]);

  const handleWordSubmit = useCallback((): void => {
    if (!submittedWords.includes(currentWord) && isValidWord(currentWord)) {
      setSubmittedWords([...submittedWords, currentWord]);
      setScore(score + currentWord.length);
      setCurrentWord('');
      if (soundRef.current) {
        soundRef.current.play().catch((error) => {
          console.error('Playback failed:', error);
        });
      }
    }
  }, [currentWord, submittedWords, isValidWord, score]);

  const handleLetterClick = useCallback((letter: string) => {
    setCurrentWord((prevWord) => prevWord + letter);
  }, []);

  const shuffleLetters = useCallback(() => {
    const shuffledLetters = letters.filter(l => l !== centralLetter);
    shuffledLetters.sort(() => Math.random() - 0.5);
    const newLetters = [...shuffledLetters.slice(0, 3), centralLetter, ...shuffledLetters.slice(3)];
    setLetters(newLetters);
  }, [letters, centralLetter]);

  const resetWordInput = useCallback(() => {
    setCurrentWord('');
  }, []);

  return (
    <div className="container">
      <div className="game-management">
        <Button variant="contained" color="secondary" onClick={endGame}>
          Avsluta Spelet
        </Button>
      </div>
      <h1>Simones Ordspel</h1>
      <div className="found-words-container" ref={foundWordsRef}>
        <h2>Hittade Ord</h2>
        <WordList words={submittedWords} />
      </div>

      {letters.length > 0 && (
        <>
          <LetterGrid letters={letters} requiredLetter={centralLetter} onLetterClick={handleLetterClick} />
          <WordInput currentWord={currentWord} onWordSubmit={handleWordSubmit} setCurrentWord={setCurrentWord} />
          {errorMessage && <p className="error-message">{errorMessage}</p>}
          
          <div className="word-info">
            <p>Antal mjliga ord: {filteredValidWords.length}</p>
            <p>Hittade ord: {submittedWords.length}/{filteredValidWords.length}</p>
          </div>
          
          <div className="controls">
            <IconButton color="primary" onClick={shuffleLetters}>
              <ShuffleIcon />
            </IconButton>
            <IconButton color="primary" onClick={resetWordInput}>
              <RestartAltIcon />
            </IconButton>
          </div>

          <Score score={score} />
        </>
      )}
    </div>
  );
}

export default Game;
