import React, { useState, useEffect } from 'react';
import Game from './components/Game/Game';
import MainMenu from './components/MainMenu/MainMenu';
import './global.css';
// eslint-disable-next-line import/no-webpack-loader-syntax
import Worker from 'worker-loader!./wordWorker.worker';

function App() {
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [validWords, setValidWords] = useState<string[]>([]);
  const [showMenu, setShowMenu] = useState(true);
  const [highScore, setHighScore] = useState<number>(0);

  useEffect(() => {
    const worker = new Worker();
    worker.postMessage('')

    worker.onmessage = (e) => {
      setValidWords(e.data);
      setIsLoading(false);
    };

    const storedHighScore = localStorage.getItem('highScore');
    if (storedHighScore) {
      setHighScore(Number(storedHighScore));
    }

    return () => {
      worker.terminate();
    };
  }, []);

  const handleStartGame = () => {
    setShowMenu(false);
  };

  const handleCancelGame = () => {
    setShowMenu(true);
  };

  const handleHighScoreUpdate = (score: number) => {
    if (score > highScore) {
      setHighScore(score);
      localStorage.setItem('highScore', score.toString());
    }
    setShowMenu(true);
  };

  return (
    <div>
      {isLoading ? (
        <div className="loading-screen">
          <h2>Laddar spel...</h2>
          <div className="spinner"></div>
        </div>
      ) : showMenu ? (
        <MainMenu highScore={highScore} onStartGame={handleStartGame} />
      ) : (
        <Game validWords={validWords} onGameEnd={handleHighScoreUpdate} onCancelGame={handleCancelGame} />
      )}
    </div>
  );
}

export default App;
